  created: function() {
    // $("#formsMenu").hide();
    // $("#uploadMenu").show();
    this.refresh();
  },
  methods: {
    refresh: function() {
      //  var forms = [];
      this.form = {};
      service.getItems("form").then(forms => {
        this.forms = forms;
        console.log(forms);
      });
    },
    selectForm(form) {
      this.form = form;

      Formio.createForm(
        document.getElementById("formData"),
        {
          components: form.formData
        },
        {
           language: "ch",
           i18n: config.i18n
        }
      ).then(function(form){
   
      });
    },
    editForm() {
      if (!this.form || !this.form._id) {
        return;
      }
      this.$router.push({
        name: "formEdit",
        query: { id: this.form._id }
      });
    },
    deleteForm() {
      if (!this.form._id) {
        return;
      }
      service.deleteItem("form",this.form._id).then(result => {
        this.refresh();
        $("#deleteModal").modal("hide");
      });
    }
  }
};
